import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import headerStyles from './header.module.css'
import linkedin from '../images/linkedin-3-32.png'
import instagram from '../images/insta_icon_32px.png'
import github from '../images/GitHub-Mark-Light-32px.png'

const Header = ({ siteTitle }) => (
  <header className={headerStyles.header} >

    <div className={headerStyles.title} >
      <h1 style={{ margin: 0 }}>
        <Link to="/" style={{
            color: `white`,
            textDecoration: `none`,
          }}>
          {siteTitle}
        </Link>
      </h1>
    </div>

    <div className={headerStyles.socialContainer}>
      <img src={instagram} className={headerStyles.socialIcon}/>
      <img src={github} className={headerStyles.socialIcon}/>
      <img src={linkedin} className={headerStyles.socialIcon}/>
     </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: `Andrew Rivera`,
}

export default Header
